function TimeOfYear() {
  const data = new Date();
  let season;
  if (data.getMonth() < 2 || data.getMonth() === 11) {return season = 'Зима' } 
  else if ( data.getMonth() >= 2 && data.getMonth() < 5 ) {return season = 'Весна'}
  else if ( data.getMonth() >= 5 && data.getMonth() < 8 ) {return season = 'Лето'}
  else {return season = 'Осень'}
}

export default TimeOfYear